  color: rgba(0, 0, 0, 0.9);
}

.header-content {
  text-align: start;
}

.input {
  position: relative;
  width: 100%;
  height: 52px;
  margin-top: 24px;
}

.your-input input {
  width: 100%;
  height: 52px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.6);
  transition: 0.2s;
  font-size: 18px;
  line-height: 1.33333;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.9);
  padding: 28px 12px 6px;
  border-radius: 4px !important;
}

.your-input label {
  position: absolute;
  top: 30%;
  left: 10px;
  font-size: 18px;
  color: #666666;
  transition: 0.2s;
}

.input input:focus ~ label,
.input input:valid ~ label {
  top: 10%;
  font-size: 0.8rem;
  color: #000;
}

.input input:focus {
  border-width: 2px;
  border-color: #0a66c3;
}

.forgot-password {
  left: -8px;
  margin-top: 15px;
  text-align: start;
}

.forgot-password-link {
  font-size: 16px;
  text-decoration: none;
  font-weight: 600;
  color: #0a66c3;
  transition: 0.3s;
  border-radius: 12px;
  padding: 8px;
}

.forgot-password-link:hover {
  background: #d0e8ff;
  text-decoration: underline;
}

.button-1 {
  height: 52px;
  line-height: 52px;
  background: #0a66c3;
  outline: none;
  border: none;
  border-radius: 30px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  flex: 1 1;
}

.button-1:hover {
  background: #004080;
  cursor: pointer;
}

.button-alternative {
  background-color: #fff;
  height: 52px;
  line-height: 52px;
  outline: none;
  border: 1px solid #666666;
  border-radius: 30px;
  color: #676767;
  font-size: 16px;
  font-weight: 600;
  flex: 1 1;
  display: flex;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
  justify-content: center;
}

.button-alternative:hover {
  border: 1px solid #616161;
  background-color: #f3f3f3;
  cursor: pointer;
}

.or-login-button {
  display: flex;
  align-items: center;
}

.or-login-button-text {
  margin-left: 5px;
}

.signup-button {
  display: flex;
