}

.copyright {
  font-size: 12px;
  line-height: 1.33333;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.9);
  padding: 0 8px 0 0;
}

.footer_nav_list {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
}

.nav_list_a_tag {
  text-decoration: none;
  font-size: 12px;
